import React, { Component } from 'react';
import Input from 'antd/es/input';
import Popover from 'antd/es/popover';
import cls from 'classnames';
import CronBuilder from '../cron-builder';
class CronInput extends Component {
    constructor(props) {
        super(props);
        this.onChange = (value) => {
            this.setState({ value }, () => {
                const { onChange } = this.props;
                if (onChange) {
                    onChange(value);
                }
            });
        };
        this.onInputChange = () => {
            this.setState({ value: undefined }, () => {
                const { onChange } = this.props;
                if (onChange) {
                    onChange();
                }
            });
        };
        const { value, defaultValue } = props;
        this.state = {
            value: value || defaultValue,
        };
    }
    static getDerivedStateFromProps(nextProps, prevState) {
        if ('value' in nextProps && nextProps.value !== prevState.value) {
            return { value: nextProps.value };
        }
        return null;
    }
    render() {
        const { style, className } = this.props;
        const { value } = this.state;
        return (<div style={style} className={cls(className, 'seid-cron-input')} ref={ref => {
            this.loader = ref;
        }}>
        <Popover trigger="click" getPopupContainer={() => (this.loader ? this.loader : document.body)} content={<div style={{ width: '600px' }}>
              <CronBuilder value={value} onChange={this.onChange}/>
            </div>}>
          <Input value={value} allowClear onChange={this.onInputChange}/>
        </Popover>
      </div>);
    }
}
export default CronInput;
